  async function fetchCollectionCounts(){
    let address = document.getElementById("addressETH").value;
    const options = {method: 'GET', headers: {Accept: 'application/json'}};
    fetch('https://api.opensea.io/api/v1/collections?asset_owner='+address+'&offset=0&limit=300', options)
      .then((response) => {
      return response.json();
      })
      .then((data) => {
        console.log(data);
        for (let i = 0; i < data.length; i++){
          collectionList.push(data[i].slug);
          countList.push(data[i].owned_asset_count);
        }
      });
    }    

  async function fetchNFTBag(){
    var tbody = document.querySelector("#tblUserBag");
    const options2 = {method: 'GET'};
     for (let i= 1; i < collectionList.length; i++){
       fetch("https://api.opensea.io/api/v1/collection/" + collectionList[i], options2)
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          let osFetch = data.collection;
          let osData = osFetch.stats;     
          totalCount += countList[i];
          //console.log(osData.floor_price);
          if (osData.floor_Price === null){
            totalBag += 0;
            userTotalBag[i-1] = [collectionList[i],countList[i],0,0];
            counter += 1;
          }
          else{
            totalBag += (countList[i] * osData.floor_price);
            userTotalBag[i-1] = [collectionList[i],countList[i],osData.floor_price,(countList[i] * osData.floor_price)];
            counter += 1;
          }
          if (counter == collectionList.length-1){
            populateTbl();
            totalBagMsg();
          }
        });
    }  
  }


